// Atom: Overlay

// variables
$ovl-class: ".#{$nameSpace}CookieOverlay" !default;
// colors
$ovl-color: rgba(0, 0, 0, 0.5) !default;

#{$ovl-class} {
  &.hidden {
    opacity: 0;
    visibility: hidden;
  }
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $ovl-color;
  z-index: 5;
}